import {SET_PUBLICKEY, GET_MY_RECENT_WINS_SUCCESS, CHANGE_CURRENT_PAGE} from '../../utils/types';

const initialState = {
    publicKey: 0,
    myRecentWin: [],
    currentPage: "play",
    loading:true
}

const userReducer = (state = initialState, action) => {

    switch(action.type){

        case SET_PUBLICKEY:
        return {
            ...state,
            publicKey: action.payload,
            loading: false

        }
        case GET_MY_RECENT_WINS_SUCCESS:
        return {
            ...state,
            myRecentWin: action.payload.map(myWinData => 
                {
                    return { 
                        bet: myWinData[0], 
                        multiplier:myWinData[1], 
                        payout:myWinData[2]
                    }
                }),
            
            loading: false
        }
        case CHANGE_CURRENT_PAGE:
        return {
            ...state,
            currentPage: action.payload
        }
        
        default: return state
    }

}

export default userReducer;